'use client'

import { useState } from 'react'
import Link from 'next/link'

interface Task {
  id: string
  projectId: string
  projectName: string
  projectColor: string
  title: string
  description: string
  status: 'backlog' | 'todo' | 'in_progress' | 'in_review' | 'blocked' | 'completed'
  priority: 'low' | 'medium' | 'high' | 'urgent'
  dueDate: string | null
  estimatedHours: number | null
  trackedHours: number
  aiGenerated: boolean
  checklistItems: number
  checklistCompleted: number
  commentCount: number
}

// Mock data - tasks assigned to current user
const mockMyTasks: Task[] = [
  { id: '1', projectId: '1', projectName: 'E-commerce Website', projectColor: '#3B82F6', title: 'Design mobile navigation', description: 'Create responsive navigation menu for mobile devices', status: 'todo', priority: 'high', dueDate: '2024-12-22', estimatedHours: 12, trackedHours: 0, aiGenerated: true, checklistItems: 4, checklistCompleted: 0, commentCount: 3 },
  { id: '2', projectId: '1', projectName: 'E-commerce Website', projectColor: '#3B82F6', title: 'Implement cart persistence', description: 'Save cart to local storage and sync with backend', status: 'todo', priority: 'high', dueDate: '2024-12-23', estimatedHours: 8, trackedHours: 0, aiGenerated: false, checklistItems: 2, checklistCompleted: 0, commentCount: 0 },
  { id: '3', projectId: '1', projectName: 'E-commerce Website', projectColor: '#3B82F6', title: 'Implement product catalog API', description: 'Build RESTful API for product listing', status: 'in_progress', priority: 'high', dueDate: '2024-12-20', estimatedHours: 24, trackedHours: 16, aiGenerated: true, checklistItems: 8, checklistCompleted: 5, commentCount: 5 },
  { id: '4', projectId: '1', projectName: 'E-commerce Website', projectColor: '#3B82F6', title: 'Homepage hero section', description: 'Design and implement animated hero section', status: 'in_review', priority: 'high', dueDate: '2024-12-18', estimatedHours: 16, trackedHours: 14, aiGenerated: true, checklistItems: 5, checklistCompleted: 5, commentCount: 6 },
  { id: '5', projectId: '1', projectName: 'E-commerce Website', projectColor: '#3B82F6', title: 'Integrate payment gateway', description: 'Connect Stripe API for payment processing', status: 'blocked', priority: 'urgent', dueDate: '2024-12-17', estimatedHours: 24, trackedHours: 4, aiGenerated: true, checklistItems: 6, checklistCompleted: 1, commentCount: 12 },
  { id: '6', projectId: '1', projectName: 'E-commerce Website', projectColor: '#3B82F6', title: 'Design system setup', description: 'Create design tokens and component library', status: 'completed', priority: 'high', dueDate: '2024-12-15', estimatedHours: 20, trackedHours: 18, aiGenerated: true, checklistItems: 10, checklistCompleted: 10, commentCount: 4 },
  { id: '7', projectId: '2', projectName: 'Mobile App', projectColor: '#8B5CF6', title: 'Build restaurant listing screen', description: 'Display nearby restaurants with filtering', status: 'in_progress', priority: 'urgent', dueDate: '2024-12-19', estimatedHours: 20, trackedHours: 12, aiGenerated: true, checklistItems: 6, checklistCompleted: 4, commentCount: 8 },
]

const STATUS_COLORS: Record<string, { bg: string; text: string; dot: string }> = {
  backlog: { bg: 'bg-gray-100', text: 'text-gray-700', dot: 'bg-gray-500' },
  todo: { bg: 'bg-blue-100', text: 'text-blue-700', dot: 'bg-blue-500' },
  in_progress: { bg: 'bg-yellow-100', text: 'text-yellow-700', dot: 'bg-yellow-500' },
  in_review: { bg: 'bg-purple-100', text: 'text-purple-700', dot: 'bg-purple-500' },
  blocked: { bg: 'bg-red-100', text: 'text-red-700', dot: 'bg-red-500' },
  completed: { bg: 'bg-green-100', text: 'text-green-700', dot: 'bg-green-500' },
}

const PRIORITY_COLORS: Record<string, string> = {
  low: 'text-gray-500 bg-gray-50',
  medium: 'text-blue-600 bg-blue-50',
  high: 'text-orange-600 bg-orange-50',
  urgent: 'text-red-600 bg-red-50',
}

export default function MyTasksPage() {
  const [tasks, setTasks] = useState<Task[]>(mockMyTasks)
  const [filter, setFilter] = useState<'all' | 'today' | 'week' | 'overdue'>('all')
  const [statusFilter, setStatusFilter] = useState<string>('all')
  const [showCompleted, setShowCompleted] = useState(false)

  // Filter tasks
  const today = new Date()
  today.setHours(0, 0, 0, 0)
  const weekFromNow = new Date(today)
  weekFromNow.setDate(weekFromNow.getDate() + 7)

  const filteredTasks = tasks.filter(task => {
    // Filter out completed unless showCompleted is true
    if (task.status === 'completed' && !showCompleted) return false
    
    // Status filter
    if (statusFilter !== 'all' && task.status !== statusFilter) return false
    
    // Date filter
    if (filter === 'today' && task.dueDate) {
      const dueDate = new Date(task.dueDate)
      dueDate.setHours(0, 0, 0, 0)
      return dueDate.getTime() === today.getTime()
    }
    if (filter === 'week' && task.dueDate) {
      const dueDate = new Date(task.dueDate)
      return dueDate >= today && dueDate <= weekFromNow
    }
    if (filter === 'overdue' && task.dueDate) {
      const dueDate = new Date(task.dueDate)
      return dueDate < today && task.status !== 'completed'
    }
    return true
  })

  // Group by status
  const tasksByStatus = {
    blocked: filteredTasks.filter(t => t.status === 'blocked'),
    in_progress: filteredTasks.filter(t => t.status === 'in_progress'),
    in_review: filteredTasks.filter(t => t.status === 'in_review'),
    todo: filteredTasks.filter(t => t.status === 'todo'),
    backlog: filteredTasks.filter(t => t.status === 'backlog'),
    completed: filteredTasks.filter(t => t.status === 'completed'),
  }

  // Stats
  const stats = {
    total: tasks.filter(t => t.status !== 'completed').length,
    inProgress: tasks.filter(t => t.status === 'in_progress').length,
    blocked: tasks.filter(t => t.status === 'blocked').length,
    dueToday: tasks.filter(t => t.dueDate && new Date(t.dueDate).toDateString() === today.toDateString()).length,
    overdue: tasks.filter(t => t.dueDate && new Date(t.dueDate) < today && t.status !== 'completed').length,
    completed: tasks.filter(t => t.status === 'completed').length,
  }

  const toggleTaskComplete = (taskId: string) => {
    setTasks(prev => prev.map(task => 
      task.id === taskId 
        ? { ...task, status: task.status === 'completed' ? 'todo' : 'completed' as const }
        : task
    ))
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <Link 
            href="/sub-account/project-management"
            className="p-2 hover:bg-gray-100 rounded-lg transition-colors"
          >
            <svg className="w-5 h-5 text-gray-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
            </svg>
          </Link>
          <div>
            <h1 className="text-3xl font-bold text-gray-900 flex items-center gap-3">
              <span className="text-2xl">ðŸ‘¤</span>
              My Tasks
            </h1>
            <p className="text-gray-500 text-sm mt-1">
              Tasks assigned to you across all projects
            </p>
          </div>
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-2 md:grid-cols-6 gap-4">
        <div className="bg-white rounded-xl border border-gray-200 p-4">
          <div className="text-sm text-gray-500">Total Active</div>
          <div className="text-2xl font-bold text-gray-900">{stats.total}</div>
        </div>
        <div className="bg-white rounded-xl border border-yellow-200 p-4">
          <div className="text-sm text-yellow-600">In Progress</div>
          <div className="text-2xl font-bold text-yellow-600">{stats.inProgress}</div>
        </div>
        <div className="bg-white rounded-xl border border-red-200 p-4">
          <div className="text-sm text-red-600">Blocked</div>
          <div className="text-2xl font-bold text-red-600">{stats.blocked}</div>
        </div>
        <div className="bg-white rounded-xl border border-blue-200 p-4">
          <div className="text-sm text-blue-600">Due Today</div>
          <div className="text-2xl font-bold text-blue-600">{stats.dueToday}</div>
        </div>
        <div className="bg-white rounded-xl border border-orange-200 p-4">
          <div className="text-sm text-orange-600">Overdue</div>
          <div className="text-2xl font-bold text-orange-600">{stats.overdue}</div>
        </div>
        <div className="bg-white rounded-xl border border-green-200 p-4">
          <div className="text-sm text-green-600">Completed</div>
          <div className="text-2xl font-bold text-green-600">{stats.completed}</div>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-xl border border-gray-200 p-4">
        <div className="flex flex-wrap items-center gap-4">
          {/* Quick Filters */}
          <div className="flex items-center gap-2">
            <button
              onClick={() => setFilter('all')}
              className={`px-3 py-1.5 rounded-lg text-sm font-medium transition-colors ${
                filter === 'all' ? 'bg-[#5E59EC] text-white' : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
            >
              All
            </button>
            <button
              onClick={() => setFilter('today')}
              className={`px-3 py-1.5 rounded-lg text-sm font-medium transition-colors ${
                filter === 'today' ? 'bg-[#5E59EC] text-white' : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
            >
              Today
            </button>
            <button
              onClick={() => setFilter('week')}
              className={`px-3 py-1.5 rounded-lg text-sm font-medium transition-colors ${
                filter === 'week' ? 'bg-[#5E59EC] text-white' : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
            >
              This Week
            </button>
            <button
              onClick={() => setFilter('overdue')}
              className={`px-3 py-1.5 rounded-lg text-sm font-medium transition-colors ${
                filter === 'overdue' ? 'bg-red-500 text-white' : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
            >
              Overdue
            </button>
          </div>

          <div className="h-6 w-px bg-gray-300"></div>

          {/* Status Filter */}
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="px-3 py-1.5 border border-gray-300 rounded-lg text-sm focus:ring-2 focus:ring-[#5E59EC] focus:border-transparent"
          >
            <option value="all">All Status</option>
            <option value="todo">To Do</option>
            <option value="in_progress">In Progress</option>
            <option value="in_review">In Review</option>
            <option value="blocked">Blocked</option>
          </select>

          <div className="flex-1"></div>

          {/* Show Completed Toggle */}
          <label className="flex items-center gap-2 cursor-pointer">
            <input
              type="checkbox"
              checked={showCompleted}
              onChange={(e) => setShowCompleted(e.target.checked)}
              className="h-4 w-4 rounded border-gray-300 text-[#5E59EC] focus:ring-[#5E59EC]"
            />
            <span className="text-sm text-gray-600">Show Completed</span>
          </label>
        </div>
      </div>

      {/* Task List Grouped by Status */}
      <div className="space-y-6">
        {/* Blocked Tasks - Show First */}
        {tasksByStatus.blocked.length > 0 && (
          <TaskSection 
            title="ðŸš§ Blocked" 
            tasks={tasksByStatus.blocked} 
            onToggleComplete={toggleTaskComplete}
            bgColor="bg-red-50"
            borderColor="border-red-200"
          />
        )}

        {/* In Progress */}
        {tasksByStatus.in_progress.length > 0 && (
          <TaskSection 
            title="ðŸ”„ In Progress" 
            tasks={tasksByStatus.in_progress} 
            onToggleComplete={toggleTaskComplete}
            bgColor="bg-yellow-50"
            borderColor="border-yellow-200"
          />
        )}

        {/* In Review */}
        {tasksByStatus.in_review.length > 0 && (
          <TaskSection 
            title="ðŸ‘€ In Review" 
            tasks={tasksByStatus.in_review} 
            onToggleComplete={toggleTaskComplete}
            bgColor="bg-purple-50"
            borderColor="border-purple-200"
          />
        )}

        {/* To Do */}
        {tasksByStatus.todo.length > 0 && (
          <TaskSection 
            title="ðŸ“‹ To Do" 
            tasks={tasksByStatus.todo} 
            onToggleComplete={toggleTaskComplete}
            bgColor="bg-blue-50"
            borderColor="border-blue-200"
          />
        )}

        {/* Backlog */}
        {tasksByStatus.backlog.length > 0 && (
          <TaskSection 
            title="ðŸ“¦ Backlog" 
            tasks={tasksByStatus.backlog} 
            onToggleComplete={toggleTaskComplete}
            bgColor="bg-gray-50"
            borderColor="border-gray-200"
          />
        )}

        {/* Completed */}
        {showCompleted && tasksByStatus.completed.length > 0 && (
          <TaskSection 
            title="âœ… Completed" 
            tasks={tasksByStatus.completed} 
            onToggleComplete={toggleTaskComplete}
            bgColor="bg-green-50"
            borderColor="border-green-200"
          />
        )}
      </div>

      {/* Empty State */}
      {filteredTasks.length === 0 && (
        <div className="text-center py-12 bg-white rounded-xl border border-gray-200">
          <div className="text-4xl mb-4">ðŸŽ‰</div>
          <h3 className="text-lg font-medium text-gray-900">No tasks found</h3>
          <p className="text-gray-500 mt-1">
            {filter === 'overdue' ? 'Great job! No overdue tasks.' : 'Try adjusting your filters.'}
          </p>
        </div>
      )}
    </div>
  )
}

// Task Section Component
function TaskSection({ 
  title, 
  tasks, 
  onToggleComplete,
  bgColor,
  borderColor
}: { 
  title: string
  tasks: Task[]
  onToggleComplete: (id: string) => void
  bgColor: string
  borderColor: string
}) {
  const today = new Date()
  today.setHours(0, 0, 0, 0)

  return (
    <div className={`rounded-xl ${bgColor} border ${borderColor} overflow-hidden`}>
      <div className="px-4 py-3 border-b border-gray-200/50 flex items-center justify-between">
        <h2 className="font-semibold text-gray-900">{title}</h2>
        <span className="text-sm text-gray-500">{tasks.length} tasks</span>
      </div>
      <div className="divide-y divide-gray-200/50">
        {tasks.map((task) => {
          const isOverdue = task.dueDate && new Date(task.dueDate) < today && task.status !== 'completed'
          
          return (
            <div 
              key={task.id} 
              className="px-4 py-3 bg-white/60 hover:bg-white transition-colors flex items-start gap-3"
            >
              <input
                type="checkbox"
                checked={task.status === 'completed'}
                onChange={() => onToggleComplete(task.id)}
                className="mt-1 h-4 w-4 rounded border-gray-300 text-[#5E59EC] focus:ring-[#5E59EC]"
              />
              <div className="flex-1 min-w-0">
                <div className="flex items-center gap-2 flex-wrap">
                  <span 
                    className={`font-medium ${task.status === 'completed' ? 'line-through text-gray-400' : 'text-gray-900'}`}
                  >
                    {task.title}
                  </span>
                  {task.aiGenerated && (
                    <span className="text-xs" title="AI Generated">ðŸ¤–</span>
                  )}
                  <span className={`px-1.5 py-0.5 text-xs font-medium rounded ${PRIORITY_COLORS[task.priority]}`}>
                    {task.priority}
                  </span>
                </div>
                <div className="flex items-center gap-3 mt-1 text-sm">
                  <span 
                    className="flex items-center gap-1"
                    style={{ color: task.projectColor }}
                  >
                    <span className="w-2 h-2 rounded-full" style={{ backgroundColor: task.projectColor }}></span>
                    {task.projectName}
                  </span>
                  {task.dueDate && (
                    <span className={isOverdue ? 'text-red-500 font-medium' : 'text-gray-500'}>
                      ðŸ“… {isOverdue ? 'Overdue: ' : ''}{new Date(task.dueDate).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}
                    </span>
                  )}
                  {task.checklistItems > 0 && (
                    <span className="text-gray-500">
                      âœ“ {task.checklistCompleted}/{task.checklistItems}
                    </span>
                  )}
                  {task.commentCount > 0 && (
                    <span className="text-gray-500">
                      ðŸ’¬ {task.commentCount}
                    </span>
                  )}
                </div>
              </div>
              <div className="text-right text-sm">
                <div className="text-gray-600">{task.trackedHours}h</div>
                <div className="text-xs text-gray-400">/ {task.estimatedHours}h</div>
              </div>
            </div>
          )
        })}
      </div>
    </div>
  )
}
